

.learn__description {
    color: #333343;
    font-size: 60px;
    line-height: 1.16;
    max-width: 787px;
    margin-bottom: 15px;

    @media screen and (max-width:767px) {
        font-size: 30px;
        line-height: 1.2;
        font-weight: 700;

        width: 100%;
    }
}

.learn__wrapper{
    @media screen and (min-width:768px) {
        display: flex;
    }
}

.learn__button:hover{
    box-shadow: -20px 20px 50px 5px rgba(0, 0, 0, 0.1);
}

.learn__wrapper-button {
    display: grid;
    justify-content: center;
}

.learn__text{
    display: grid;
    align-items: center;

    content: '';
    text-align: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.21;
    color: #fff;
    width: 300px;
    height: 151px;
    padding: 39px 11px 45px 11px;

    
    background-image: url('../images/Rectangle_formobile.png');

    @media screen and (min-width:768px) {
    background-image: url('../images/figure.png');
    padding: 24px 32px 33px 43px;
    width: 537px;
    height: 203px;
    font-size: 24px;
    line-height: 1.33;
    }
}

.learn__button{
position: relative;
left: 80px;
top: -40px;

padding: 11px 27px 13px 28px;
width: 150px;
height: 44px;

font-size: 16px;
line-height: 1.25;
background: linear-gradient(180deg, #49495F 0%, #333343 100%);
border-radius: 40px;
color: #fff;
text-align: center;
border: 0px;

justify-content: center;
align-items: center;
}

.learn__list{
    max-height: 64px;
    display: flex;
    justify-content: space-between;
}

.learn__list-video{
    gap:30px;
    flex-wrap: wrap;
    justify-content: center;

    display: flex;
    
    @media screen and (min-width:767px) {
        margin-top: 70px;
    }
}

.learn__video{
    display: grid;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;

    max-width: 360px;
    width: 100%;
    height: 148px;
    padding: 20px 17px 20px 34px;
    
    background-color: #89BF19;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.2;
    border-radius: 40px;
}

.learn__video:hover {
    box-shadow: -20px 20px 50px 5px rgba(0, 0, 0, 0.1);
}

.learn__video-link{
display: flex;
justify-content: center;
align-items: center;

margin-left: auto;
margin-right: auto;

width: 113px;
height: 33px;
font-weight: 400;
color: #333343;

font-size: 16px;
line-height: 1.25;


text-align: center;

background: #FFFFFF;
border-radius: 40px;
}

.learn__video-img{
    margin-right: 6px;
}


