.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  visibility: visible;
  transition: 250ms;
  transform: scale(1);

  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 9999;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: scale(2);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;

  padding: 40px;

  background-color: #fff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  transform: translate(-50%, -50%) scale(1);
  z-index: 9999;

  
}

@media screen and (min-width: 1200px) {
  .modal{
  max-width: 728px;
  max-height: 781px;
  width: 100%;
}
}

@media screen and (max-width: 1199px) {
  .modal{
  max-width: 450px;
  max-height: 609px;
  width: 100%;
  }
}

.modal__close {
  display: inline-flex;
  transition: 250ms;

  position: absolute;
  right: 8px;
  top: 8px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;

  width: 30px;
  height: 30px;

  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  fill: #000000;
  cursor: pointer;

}

.modal__title {
  margin-bottom: 50px;

  color: #000000;
  font-size: 26px;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.03em;
  font-weight: 700;

  @media screen and (min-width: 1200px) {
    line-height: 1.15;
  }
}

/* Form field */

.form__field {
  margin-bottom: 10px;
}

.form__label {
  display: block;
  margin-bottom: 4px;

  font-size: 18px;
  line-height: 1.16;
  letter-spacing: 0.01em;
}

.form__inner {
  position: relative;
}

.form__input {
  resize: none;
  padding: 12px 42px 12px;

  width: 100%;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;

  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  outline: transparent;

  
}

.form__input:hover,
  .form__input:hover.textarea {
    border: 1px solid #89bf19;
    border-radius: 4px;
    fill: #89bf19;
  }

  .form__input:focus + .icon-modal {
    fill: #89bf19;
  }

.form__input.textarea {
  resize: none;
  padding: 12px 16px 12px;

  width: 100%;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;

  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
}

.form__icon {
  position: absolute;
  top: 50%;
  left: 14px;
  transform: translateY(-50%);
  fill: #89bf19;
  transition: 250ms;
}

.modal__button {
  display: flex;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  z-index: 9999;
}